.main-bar {
  background-color: #c8c8c8; }
  .main-bar .menu {
    height: 50px;
    text-align: center;
    max-width: 700px;
    margin: auto; }
    .main-bar .menu .menu-item {
      display: inline-block;
      font-weight: bold;
      padding: 15px 0px;
      text-decoration: none;
      color: #343535;
      width: 25%; }
    .main-bar .menu .menu-item:hover {
      background-color: #dfdfdf; }
    .main-bar .menu .menu-item-selected {
      background-color: #8c8c8c; }
