.product-categories-min-main {
  background-color: #ffffff; }

.product-categories-min-menu {
  height: 50px;
  text-align: center;
  max-width: 700px;
  margin: auto; }
  .product-categories-min-menu .product-categories-min-menu-item {
    display: inline-block;
    font-weight: bold;
    padding: 7px 0px;
    text-decoration: none;
    width: 25%;
    height: 50px; }
  .product-categories-min-menu .product-categories-min-menu-icon {
    background-color: #636363;
    border-radius: 50%;
    padding: 7px;
    color: #f3f3f2; }
  .product-categories-min-menu .product-categories-min-menu-item:hover {
    background-color: #dfdfdf; }
  .product-categories-min-menu .product-categories-min-menu-item-selected {
    background-color: #e0e0e0; }
